import React from 'react'
import BreadCrumbs from '../../component/BreadCrumbs';
import { Outlet } from 'react-router-dom';


const bondsPolicyComponents = [
  { link: "/dashboard/task/production/policy/bonds", text: "Policy Information" },
  {
    link: "/dashboard/task/production/policy/bonds/policy-premium",
    text: "Policy Premium",
  },
];

export default function BondsPolicy() {
  return (
    <div>
      <BreadCrumbs list={bondsPolicyComponents} />
      <Outlet />
    </div>
  );
}